<template>
  <v-form v-model="valid" ref="form" class="child-body">
    <confirm ref="confirm"></confirm>
    <info ref="info"></info>
    <loading v-if="fullscreenLoading"></loading>
    <offline @detected-condition="handleConnectivityChange"></offline>
    <v-layout row>
      <v-card flat>
        <h2 class="d-flex align-center">
          <v-icon x-large>add</v-icon>Create Installment Application
        </h2>
      </v-card>
    </v-layout>
    <v-layout row>
      <v-divider></v-divider>
    </v-layout>
    <v-spacer class="formsSpacer"></v-spacer>
    <v-layout row>
      <v-flex>
        <v-card class="search-filter-vcard ma-md-5" flat tile>
          <v-layout row wrap align-center>
            <v-flex xs5 sm3 md3 class="input-label text-xs-left text-sm-right">
              <span>Application #:</span>
            </v-flex>
            <v-flex xs12 sm6 md6>
              <v-text-field
                value="Generate once application created successfully."
                disabled
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row wrap align-center>
            <v-flex xs5 sm3 md3 class="input-label text-xs-left text-sm-right">
              <span>Branch: <b>*</b></span>
            </v-flex>
            <v-flex xs12 sm6 md6>
              <v-select
                v-model="personalInfo.Branch"
                label="Select"
                :rules="rPersonalInfo.Branch"
                required
              ></v-select>
            </v-flex>
          </v-layout>
          <v-layout row wrap align-center>
            <v-flex xs5 sm3 md3 class="input-label text-xs-left text-sm-right">
              <span>Client Name: <b>*</b></span>
            </v-flex>
            <v-flex xs12 sm6 md6>
              <v-text-field
                v-model="personalInfo.Name"
                clearable
                :rules="rPersonalInfo.Name"
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row wrap align-center>
            <v-flex xs5 sm3 md3 class="input-label text-xs-left text-sm-right">
              <span>Birth Date: <b>*</b> </span>
            </v-flex>
            <v-flex xs12 sm3 md3>
              <v-menu
                v-model="chooseBirthDate"
                transition="scale-transition"
                :close-on-content-click="false"
                lazy
                offset-y
                full-width
                style="min-width: initial !important"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="personalInfo.BirthDate"
                    v-on="on"
                    append-icon="event"
                    readonly
                    placeholder="YYYY-MM-DD"
                    color="red"
                    :rules="rPersonalInfo.BirthDate"
                    required
                  >
                  </v-text-field>
                </template>
                <v-date-picker
                  class="customTable"
                  color="red"
                  v-model="personalInfo.BirthDate"
                  @input="chooseBirthDate = false"
                >
                </v-date-picker>
              </v-menu>
            </v-flex>
          </v-layout>
          <v-layout row wrap align-center>
            <v-flex xs12 sm3 md3 class="input-label text-xs-left text-sm-right">
              <span>Age: <b>*</b></span>
            </v-flex>
            <v-flex xs12 sm3 md3>
              <v-text-field
                v-model="personalInfo.Age"
                clearable
                :rules="rPersonalInfo.Age"
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row justify-end>
            <v-btn @click="cancel" class="btn_secondary">
              <v-icon>keyboard_return</v-icon>
              Cancel
            </v-btn>
            <v-btn @click="Validate" class="btn_primary">
              <v-icon>save</v-icon>
              Save
            </v-btn>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row wrap justify-start>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Birthplace <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.BirthPlace"
              color="purple darken-2"
              label="Birth Place"
              single-line
              outline
              clearable
              :rules="rPersonalInfo.BirthPlace"
              required
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2"
              >Home Address (Present) <b class="red--text">*</b></b
            >
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.HomeAddress"
              color="purple darken-2"
              label="Home Address (Present)"
              single-line
              outline
              clearable
              :rules="rPersonalInfo.HomeAddress"
              required
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Previous Address <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.PreviousAddress"
              color="purple darken-2"
              label="Previous Address"
              single-line
              outline
              clearable
              :rules="rPersonalInfo.PreviousAddress"
              required
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Cellphone Number <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.Phone"
              color="purple darken-2"
              label="Cellphone Number"
              single-line
              outline
              clearable
              :rules="rPersonalInfo.Phone"
              required
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Telephone Number</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.Telephone"
              color="purple darken-2"
              label="Telephone Number"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Email Address <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.Email"
              color="purple darken-2"
              label="email@domain.com"
              single-line
              outline
              clearable
              :rules="rPersonalInfo.Email"
              required
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Facebook Account</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.Facebook"
              color="purple darken-2"
              label="facebook.com/"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-divider></v-divider>
    </v-layout>
    <v-layout row wrap justify-start>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2"
              >Stability of Residence <b class="red--text">*</b></b
            >
          </v-card-title>
          <v-container class="mt-4">
            <v-radio-group
              v-model="personalInfo.Residence"
              :rules="rPersonalInfo.Residence"
              required
            >
              <v-radio label="Owned" value="owned"></v-radio>
              <v-radio label="Rented" value="rented"></v-radio>
              <v-radio
                label="Living with Parents"
                value="livingWithParents"
              ></v-radio>
              <v-radio label="Free Housing" value="freeHousing"></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">If Renting</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="personalInfo.RentingName"
              label="Landlord Name"
              single-line
              outline
              clearable
            ></v-text-field>
            <v-text-field
              v-model="personalInfo.RentingAddress"
              label="Address"
              single-line
              outline
              clearable
            ></v-text-field>
            <v-text-field
              v-model="personalInfo.RentingTelNo"
              label="Tel No."
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Status <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-radio-group
              justify-center
              v-model="personalInfo.Status"
              :rules="rPersonalInfo.Status"
              required
            >
              <v-radio label="Single" value="single"></v-radio>
              <v-radio label="Married" value="married"></v-radio>
              <v-radio label="Widow" value="widow"></v-radio>
              <v-radio label="Separated" value="separated"></v-radio>
              <v-radio
                label="Live-In Or Common Law Partner"
                value="live-in"
              ></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Land <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-radio-group
              justify-center
              v-model="personalInfo.Land"
              :rules="rPersonalInfo.Land"
              required
            >
              <v-radio label="Owned" value="owned"></v-radio>
              <v-radio label="Government" value="government"></v-radio>
              <v-radio label="Mortgage" value="mortgage"></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">House Made <b class="red--text">*</b></b>
          </v-card-title>
          <v-container class="mt-4">
            <v-radio-group
              justify-center
              v-model="personalInfo.HouseMade"
              :rules="rPersonalInfo.HouseMade"
              required
            >
              <v-radio label="Concrete" value="concrete"></v-radio>
              <v-radio label="Wood" value="wood"></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
    <!-- title - source of income -->
    <v-layout mt-5 row class="title-bg">
      <v-card flat>
        <h2 class="title-header">Source of Income</h2>
      </v-card>
    </v-layout>
    <v-layout row>
      <v-divider></v-divider>
    </v-layout>
    <!-- select source of income -->
    <v-layout column>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Industry</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-select
              v-model="industry"
              solo
              color="black"
              label="Select"
            ></v-select>
          </v-container>
        </v-card>
      </v-flex>
      <v-layout wrap>
        <v-checkbox
          label="Employed"
          value="employed"
          v-model="cbSOI"
          class="mr-5"
          @change="EmployedChange"
          :rules="hasSelectSOI"
          hide-details
        ></v-checkbox>
        <v-checkbox
          label="Self-Employed"
          value="selfEmployed"
          v-model="cbSOI"
          class="mr-5"
          :rules="hasSelectSOI"
          hide-details
        ></v-checkbox>
        <v-checkbox
          label="Own Business"
          value="ownBusiness"
          v-model="cbSOI"
          class="mr-5"
          :rules="hasSelectSOI"
          hide-details
        ></v-checkbox>
        <v-checkbox
          label="Pension"
          value="pension"
          v-model="cbSOI"
          class="mr-5"
          :rules="hasSelectSOI"
          hide-details
        ></v-checkbox>
        <v-checkbox
          label="Remittance"
          value="remittance"
          v-model="cbSOI"
          class="mr-5"
          :rules="hasSelectSOI"
          hide-details
        ></v-checkbox>
      </v-layout>
      <span v-if="cbSOI.length <= 0" class="v-messages error--text"
        >At least one item should be selected</span
      >
    </v-layout>
    <!-- employed -->
    <v-layout row wrap justify-start v-if="cbSOI.includes('employed')">
      <v-flex xs12><h3 class="headline text-center">Employed</h3></v-flex>
      <v-flex xs12>
        <v-divider></v-divider>
      </v-flex>
      <v-flex xs12>
        <v-radio-group v-model="employed.TypeOf" row xs12>
          <v-radio label="Private" value="private"></v-radio>
          <v-radio label="Government" value="government"></v-radio>
        </v-radio-group>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Where Employed (Present) </b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="employed.Present"
              :rules="rEmployed.Present"
              color="purple darken-2"
              label="Where Employed (Present)"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">How Long (months)</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              :rules="rEmployed.HowLong"
              v-model="employed.HowLong"
              color="purple darken-2"
              label="How Long (months)"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Position</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              :rules="rEmployed.Position"
              v-model="employed.Position"
              color="purple darken-2"
              label="Position"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Present Business Address</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              :rules="rEmployed.PresentAddress"
              v-model="employed.PresentAddress"
              color="purple darken-2"
              label="Present Business Address"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Telephone Number</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="employed.Telephone"
              color="purple darken-2"
              label="Telephone Number"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Salary per Month</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="employed.Salary"
              color="purple darken-2"
              label="Salary per Month"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Previous Employment (if applicable) </b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="employed.Previous"
              color="purple darken-2"
              label="Previous Employment (if applicable)"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Previous Business Address (if applicable) </b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="employed.PreviousAddress"
              color="purple darken-2"
              label="Previous Business Address (if applicable)"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
    <!-- self employed/own business -->
    <v-layout
      row
      wrap
      justify-start
      v-if="cbSOI.includes('selfEmployed') || cbSOI.includes('ownBusiness')"
    >
      <v-flex
        xs12
        v-if="cbSOI.includes('selfEmployed') && cbSOI.includes('ownBusiness')"
        ><h3 class="headline text-center">
          Self-Employed / Own Business
        </h3></v-flex
      >
      <v-flex xs12 v-else-if="cbSOI.includes('selfEmployed')"
        ><h3 class="headline text-center">Self-Employed</h3></v-flex
      >
      <v-flex xs12 v-else-if="cbSOI.includes('ownBusiness')"
        ><h3 class="headline text-center">Own Business</h3></v-flex
      >
      <v-flex xs12>
        <v-divider></v-divider>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Nature of Business</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-select
              v-model="selfOwn.BusinessNature"
              solo
              color="black"
              label="Select"
              hide-details
            ></v-select>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Business Name</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.BusinessName"
              color="purple darken-2"
              label="Business Name"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Business Address</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.BusinessAddress"
              color="purple darken-2"
              label="Business Address"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Capital</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.Capital"
              color="purple darken-2"
              label="P 00,000.00"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Phone Number</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.PhoneNumber"
              color="purple darken-2"
              label="Phone Number"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">Monthly Income</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.Net"
              color="purple darken-2"
              label="Monthly Income (Net)"
              single-line
              outline
              clearable
            ></v-text-field>
            <v-text-field
              v-model="selfOwn.Gross"
              color="purple darken-2"
              label="Monthly Income (Gross)"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 md4 pa-2>
        <v-card class="info-card">
          <v-card-title class="grey lighten-2 index-label">
            <b class="body-2">How Long</b>
          </v-card-title>
          <v-container class="mt-4">
            <v-text-field
              v-model="selfOwn.HowLong"
              color="purple darken-2"
              label="How Long"
              single-line
              outline
              clearable
            ></v-text-field>
          </v-container>
