        TypeOf: "",
        Present: "",
        HowLong: "",
        Position: "",
        PresentAddress: "",
        Telephone: "",
        Salary: "",
        Previous: "",
        PreviousAddress: "",
      },
      selfOwn: {
        BusinessNature: "",
        BusinessName: "",
        BusinessAddress: "",
        Capital: "",
        PhoneNumber: "",
        Net: "",
        Gross: "",
        HowLong: "",
      },
