        Agency: "",
        Monthly: "",
      },
      remittance: {
        Name: "",
        Location: "",
        Relationship: "",
        MonthlyAmount: "",
        Frequency: "",
      },

      spouse: {
        Name: "",
        Age: "",
        IncomeSource: "",
        OfficeAddress: "",
        Position: "",
        HowLong: "",
        OfficeTelNo: "",
        Salary: "",
      },

      parents: {
        Father: {
          Name: "",
          Age: "",
          Address: "",
          Facebook: "",
          Employment: "",
          OfficeAddress: "",
          Position: "",
          HowLong: "",
        },
        Mother: {
          Name: "",
          Age: "",
          Address: "",
          Facebook: "",
          Employment: "",
          OfficeAddress: "",
          Position: "",
          HowLong: "",
        },
      },
      parentsInLaw: {
        Father: {
          Name: "",
          Age: "",
          Address: "",
          Facebook: "",
          Employment: "",
          OfficeAddress: "",
          Position: "",
          HowLong: "",
        },
        Mother: {
          Name: "",
          Age: "",
          Address: "",
          Facebook: "",
          Employment: "",
          OfficeAddress: "",
          Position: "",
          HowLong: "",
        },
      },

      //VALIDATION
      rEmployed: {
        Present: [(v) => !!v || "Present Employment is required"],
        PresentAddress: [(v) => !!v || "Present business address is required"],
        HowLong: [(v) => !!v || "How Long is required"],
        Position: [(v) => !!v || "Position is required"],
      },
      rSelfOwn: {
        Present: [(v) => !!v || "Present Employment is required"],
        PresentAddress: [(v) => !!v || "Present business address is required"],
        HowLong: [(v) => !!v || "How Long is required"],
        Position: [(v) => !!v || "Position is required"],
      },
      rParents: {
        Father: {
          Name: [(v) => !!v || "Father's Name is required"],
