      return [this.cbSOI.length > 0 || ""];
    },
    ...mapGetters({
      model: constants.templateModel,
      branches: constants.branchesList,
    }),
  },
  methods: {
    Validate() {
      if (this.$refs.form.validate()) {
        Alert("Valid");
      } else {
        this.$nextTick(() => {
          const el = this.$el.querySelector(
            ".v-messages.error--text:first-of-type"
          );
          this.$vuetify.goTo(el);
          return;
        });
      }
    },
    EmployedChange(e) {
      if (e) {
        this.rEmployed.Present = [
          (v) => !!v || "Present Employment is required",
        ];
        this.rEmployed.PresentAddress = [
          (v) => !!v || "Present business address is required",
        ];
        this.rEmployed.HowLong = [(v) => !!v || "How Long is required"];
        this.rEmployed.Position = [(v) => !!v || "Position is required"];
      } else {
        this.rEmployed.Present = "";
        this.rEmployed.PresentAddress = "";
        this.rEmployed.HowLong = "";
        this.rEmployed.Position = "";
      }
    },
    pilExpandable() {
      this.skipParentLaw = !this.skipParentLaw;
    },
    childExpandable() {
      this.skipChildren = !this.skipChildren;
    },
    spouseExpandable() {
      this.skipSpouse = !this.skipSpouse;
    },
    listUploads(e) {
      this.showUploads = true;
      let uploads = e.srcElement.files;
      let temp = uploads.length;

      if (this.countAttach + uploads.length > 5) {
        this.$refs.info
          .open(constants.message, constants.attachInfo, {
            color: constants.error_color,
          })
          .then(() => {});
        temp = 5 - this.countAttach;
        this.countAttach += temp;
      } else {
        this.countAttach += uploads.length;
      }
      for (let index = 0; index < temp; index++) {
        this.files.push(uploads[index]);
      }
      console.log(this.files);
    },
    delFile(index) {
      this.files.splice(index, 1);
      this.countAttach--;
      console.log(this.files);
    },

    resetFileField(e) {
      console.log("reset");
      const fileInput = this.$refs["file-input"];
      console.log(fileInput);
      fileInput.value = "";
      this.files = [];
      this.countAttach = 0;
    },
    addHistoryRow() {
      this.listCreditHistory.push({
        Name: "",
        Type: "",
        Date: new Date().toISOString().substr(0, 10),
        Terms: "",
        Balance: "",
      });
    },
    delHistoryRow(index) {
      this.listCreditHistory.splice(index, 1);
    },
    addPropertyRow() {
      this.listPropertyOwned.push({
        Name: "",
      });
    },
    delPropertyRow(index) {
      this.listPropertyOwned.splice(index, 1);
    },
    addUnitDesiredRow() {
      this.listUnitDesired.push({
        Brand: "",
        Terms: "",
        DownPayment: "",
        MonthlyInstallment: "",
        TotalPrice: "",
        TotalRebate: "",
        Remarks: "",
      });
    },
    delUnitDesiredRow(index) {
      this.listUnitDesired.splice(index, 1);
    },
    addUnitDesiredTCRow() {
      this.listUnitDesiredTC.push({
        Brand: "",
        Serial: "",
        Code: "",
        Amount: "",
        Accounting: "",
      });
    },
    delUnitDesiredTCRow(index) {
      this.listUnitDesiredTC.splice(index, 1);
    },
    addChildRow() {
      this.listChildren.push({
        Name: "",
        Age: "",
        HomeAddress: "",
        Telephone: "",
        EmploymentOrSchool: "",
        Address: "",
        PositionOrGrade: "",
        HowLong: "",
      });
    },
    delChildRow(index) {
      this.listChildren.splice(index, 1);
    },
    cancel() {
    },
    clearStore() {
      this.$store.dispatch(constants.clearLogin);
      this.$store.dispatch(constants.clearUsers);
      this.$store.dispatch(constants.clearAccounts);
      this.$store.dispatch(constants.clearEmails);
      this.$store.dispatch(constants.clearJobOrders);
      this.$store.dispatch(constants.clearCases);
      this.$store.dispatch(constants.clearRating);
      this.$store.dispatch(constants.clearTemplate);
      this.$router.push("/login");
    },
    handleConnectivityChange(status) {
      if (status === false) {
        this.status = false;
        this.$refs.info
          .open(constants.message, constants.noInternet, {
            color: constants.error_color,
          })
          .then(() => {});
      } else {
        this.status = true;
      }
    },
  },

};
