}

.border-none > .v-input__control > .v-input__slot:before {
  border-style: none !important;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}
@media screen and (max-width: 960px) {
  .table-responsive {
    overflow-x: auto;
  }
}

.file-upload {
  background: white;
  box-shadow: var(--box-shadow);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.card {
  background: white;
  box-shadow: var(--box-shadow);
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  transition: all 200ms ease;
  position: relative;
}

.is-uploading .upload-overlay {
  display: flex;
}

.upload-overlay {
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  color: white;
  font-size: 2rem;
  font-weight: bold;
  display: none;
  right: 0;
  align-items: center;
  justify-content: space-around;
}

.card-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

[type="submit"] {
  display: block;
  background: darken(cornflowerblue, 10%);
  color: white;
  appearance: none;
  width: 100%;
  border: 0;
  padding: 1rem;
  font-size: 1.4rem;
}

.thumb img {
  width: 7rem;
  height: 9rem;
  display: block;
  margin-bottom: 0;
  object-fit: cover;
  align-self: stretch;
}

.hidden {
  opacity: 0;
  transform: translateY(-20%);
  transition: all 200ms ease;
}

[type="checkbox"] {
  display: none;
}
[type="checkbox"] ~ label {
  display: block;
  border: 2px solid var(--black);
  padding: 1rem;
  border-radius: 2rem;
  margin: 0 20px;
  opacity: 0.4;
  position: relative;
}
[type="checkbox"] ~ label:before {
  content: "\f00c";
  font-size: 1.2rem;
  font-family: "FontAwesome";
  display: block;
  color: var(--black);
  position: absolute;
  transform: scale(0.8);
  transform-origin: 50% 50%;
  opacity: 0;
  transition: scale 300ms var(--easing-cubic-bezier),
    opacity 300ms var(--easing-cubic-bezier);
  left: 0.4rem;
  top: 0.4rem;
}

[type="checkbox"]:checked ~ label:before {
  /* transform: translateX(.4rem) translateY(.4rem) scale(1.1); */
  transform: scale(1.3);
  transition: scale 300ms var(--easing-cubic-bezier),
    opacity 300ms var(--easing-cubic-bezier);
  opacity: 1;
}

.checkbox-sub-label {
  display: block;
  color: var(--black);
  opacity: 0.4;
  font-size: 11px;
  text-align: center;
  margin: 0.6rem;
}

.remove-card {
  flex-grow: 2;
  align-self: stretch;
  display: flex;
  flex-basis: 10%;
  flex-grow: 0;
}
.remove-card-btn {
  appearance: none;
}
.remove-card-btn:hover {
  text-decoration: underline;
}

.checkbox-wrap {
  background: var(--green);
  align-self: stretch;
  display: flex;
  padding-top: 1rem;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

/* vue transitions */

/* softIn */
.softIn-enter {
  opacity: 0;
  transform: translateY(-20%);
}

.softIn-enter-to {
  opacity: 1;
  transform: translateY(0);
}

.softIn-enter-active {
  transition: all 200ms ease;
}

.softIn-leave {
  opacity: 1;
  transform: translateY(0);
}

.softIn-leave-to {
  opacity: 0;
  transform: translateY(-20%);
}

.softIn-leave-active {
  transition: all 200ms ease;
}

.softIn-move {
  transition: all 200ms ease;
}

.btn-upload {
  color: -webkit-link;
  cursor: pointer;
  text-decoration: underline;
}

.title-flex {
  display: flex;
  align-items: center;
}
.title-header {
  padding: 10px;
  background-color: #ff0000;
  color: #fff;
  font-size: 20px;
}
.title-bg {
  background-color: #ff0000;
}

.expandable-icon {
  border-radius: 10px;
  border: 1px solid #fff;
}

.text-center {
  text-align: center;
}
</style>



